import Vue from "vue"

Vue.prototype.localStorageValName = "__localstorage_name__"

Vue.prototype.imageUploadPath = "http://62.234.101.252:1234/file/uploadImage"
Vue.prototype.dataUploadPath = "http://62.234.101.252:1234/file/uploadFile"


Vue.prototype.fileDownloadPath = "http://62.234.101.252:1234/file/download"

Vue.prototype.rfc = (s) => {
  s = s || ''
  s = s.replace("{", "%7B")
  s = s.replace("}", "%7D")
  return s

}


Vue.prototype.shortStr = (s, maxLen = 5) => {
  s = s || ''
  if (s.length <= maxLen) {
    return s
  } else {
    return s.substring(0, maxLen) + '...'
  }
}


Vue.prototype.getls = (key) => {
  // 如果给定key值
  let localStorageObj = localStorage.getItem(Vue.prototype.localStorageValName)
  localStorageObj = JSON.parse(localStorageObj)
  if (key) {
    let val = localStorageObj.key
    return val
  } else {
    return localStorageObj
  }
  return localStorage
}


Vue.prototype.setls = (k, v) => {
  let localStorageObj = localStorage.getItem(Vue.prototype.localStorageValName)
  localStorageObj = JSON.parse(localStorageObj)
  localStorageObj.k = v
  localStorageObj = JSON.stringify(localStorageObj)
  localStorage.setItem(Vue.prototype.localStorageValName, localStorageObj)
}

Vue.prototype.clearls = () => {
  localStorage.setItem(Vue.prototype.localStorageValName, '')
}

// 内容编码
Vue.prototype.ec = (c) => {
  return
}

// 保留2位小数
Vue.prototype.round2 = (num) => {
  num = '' + num

  let i = num.indexOf('.')
  if (i < 0) {
    return num
  } else {
    return num.substring(0, i + 2)
  }


}
// 将一个数字转化为kb/gb或者多少kb/gb等转化为数字
Vue.prototype.fsize = (v) => {
  v = v || ''
  v = v + ''
  v = v.toLowerCase()

  let reg = /(^\d+)([(b)(kb)(mb)(gb)(tb)]{0,2})$/i

  let vs = v.match(reg)

  // 如果匹配失败
  if (!vs) {
    return "ERROR"
  }

  let size = parseInt(vs[1])
  let dw = vs[2]

  // 如果是纯数字
  if (dw == "") {
    // 小于1kb显示多少b
    if (size < 1024) return size + 'b'
    // 大于1kb小于1000kb，显示多少kb
    if (size < 1000 * 1024) return (size / (1 * 1024)).toFixed(2) + 'KB'
    if (size < 1000 * 1024 * 1024) return (size / (1024 * 1024)).toFixed(2) + 'MB'
    if (size < 1000 * 1024 * 1024 * 1024) return (size / (1024 * 1024 * 1024)).toFixed(2) + 'GB'
    if (size < 1000 * 1024 * 1024 * 1024 * 1024) return (size / (1024 * 1024 * 1024 * 1024)).toFixed(2) + 'TB'
  } else {
    // 如果是字符
    if (dw == 'b') return size * 1024
    if (dw == 'kb') return size * 1024 * 1024
    if (dw == 'mb') return size * 1024 * 1024 * 1024
    if (dw == 'gb') return size * 1024 * 1024 * 1024 * 1024
    if (dw == 'tb') return size * 1024 * 1024 * 1024 * 1024 * 1024
  }
}


// 内容解码
Vue.prototype.dc = (c) => { }

// 格式化日期
Vue.prototype.fdate = (d, s = "/") => {
  let d2 = new Date(d)
  let dateStr = d2.getFullYear()
  dateStr += s
  dateStr += (d2.getMonth() + 1)
  dateStr += s
  dateStr += d2.getDay()
  return dateStr
}



