import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)


export const constantRouterMap = [
  {
    path: "/test",
    component: () => import("@/test/testFastJson")
  },{
    path: "/",
    component: () => import("@/pages/home")
  },
  {
    path: "/back",
    component: () => import("@/pages/back")
  },
  {
    path: "/cDetail",
    component: () => import("@/pages/commonDetail")
  },
  // {
  //   path: "/solutionDetail",
  //   component: () => import("@/pages/solutionDetail")
  // },
  {
    path: "/solution",
    component: () => import("@/pages/solution")
  },
  {
    path: "/readBlog",
    component: () => import("@/pages/readBlog")
  },

  {
    path: "/blogs",
    component: () => import("@/pages/blogs")
  },
  {
    path: "/recruit",
    component: () => import("@/pages/recruit")
  },
  {
    path: "/about",
    component: () => import("@/pages/about")
  },
  {
    path: "/create",
    component: () => import("@/pages/create/createFast")
  },
  {
    path: "/datas",
    component: () => import("@/pages/datas")
  },
  {
    path: "/detail",
    component: () => import("@/pages/detail")
  },



  {
    path: '/login',
    component: () => import('@/views/login/index'),
    hidden: true
  },
  {
    path: '/404',
    component: () => import('@/views/404'),
    hidden: true
  },


  {
    path: '*',
    redirect: '/404',
    hidden: true
  }
]

export default new Router({
  // mode: 'history', //后端支持可开
  scrollBehavior: () => ({
    y: 0
  }),
  routes: constantRouterMap
})



