import request from '@/utils/request'

export function login(p) {
  return request({
    url: '/simpleLogin/login',
    method: 'get',
    params:p
  })
}

export function userinfo() {
  return request({
    url: '/simpleLogin/userinfo',
    method: 'get',
  })
}

export function logout() {
  return request({
    url: '/simpleLogin/logout',
    method: 'get'
  })
}

export function sendEmailActiveCode(p) {
  return request({
    url: '/simpleLogin/sendEmailActiveCode',
    method: 'get',
    params:p
  })
}
export function register(p) {
  return request({
    url: '/simpleLogin/register',
    method: 'get',
    params:p
  })
}



