import axios from 'axios'
import { Message, MessageBox } from 'element-ui'
import store from '../store'
import { getToken } from '@/utils/auth'



axios.defaults.withCredentials = true
// 创建axios实例
const service = axios.create({
  baseURL: process.env.BASE_API, // api的base_url
  timeout: 15000 // 请求超时时间
})

// request拦截器
service.interceptors.request.use(config => {

  let token = JSON.parse(localStorage.mall || '{}').token
  if (token) {

    config.headers['Authorization'] = "token"
    config.headers['token'] = token

    // config.defaults.headers.token="bbbb"
    // config.headers.token="vvvvvv"
    // config.defaults.headers['token']="xxx"
    // config.headers['token'] = JSON.parse(localStorage.mall).token || 'nnnn' // 让每个请求携带自定义token 请根据实际情况自行修改
    // config.defaults.headers.common['token'] = 'Authorization Value'
  }
  return config
}, error => {
  // Do something with request error
  console.log(error) // for debug
  Promise.reject(error)
})

// respone拦截器
service.interceptors.response.use(
  response => {
    /**
    * code为非200是抛错 可结合自己业务进行修改
    */
    const res = response.data

    // 如果code==601 代表权限不足
    if (res.code == 601) {
      MessageBox.confirm('提示', '您的权限不足，无法访问该数据', {
        confirmButtonText: '重新登录',
        cancelButtonText: '取消',
        type: 'warning'
      })
      return Promise.reject('error')
    }



    if (res.code !== 200) {
      Message({
        message: res.message,
        type: 'error',
        duration: 3 * 1000
      })

      // 401:未登录;
      if (res.code === 401 || res.code === 403) {
        MessageBox.confirm('你已被登出，可以取消继续留在该页面，或者重新登录', '确定登出', {
          confirmButtonText: '重新登录',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          store.dispatch('FedLogOut').then(() => {
            location.reload()// 为了重新实例化vue-router对象 避免bug
          })
        })
      }
      return Promise.reject('error')
    } else {
      return response.data
    }
  },
  error => {
    console.log('err' + error)// for debug
    Message({
      message: error.message,
      type: 'error',
      duration: 3 * 1000
    })
    return Promise.reject(error)
  }
)

window.axios = service
export default service



