// 该文件用于页面之间传值
const heap = {
  state: {
    heapData: {}
  },

  getters: {
    getHeap(state) {
      return state.heapData
    }
  },
  mutations: {
    setHeap(state, obj) {
      let key = obj.key
      let val = obj.val
      state.heapData[key] = val
    },
  },

}

export default heap



